import { HttpClient } from '@angular/common/http'
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ActivatedRoute, Router } from '@angular/router'
import { faFileAlt } from '@fortawesome/pro-regular-svg-icons'
import { faCaretRight } from '@fortawesome/pro-solid-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { sortBy } from 'lodash'
import { forkJoin, of, Subject, Subscription } from 'rxjs'
import { flatMap, tap } from 'rxjs/operators'
import {
    Allocation,
    File,
    ICurrency,
    IUser,
    Paginated,
    Product,
    ProductFile,
    Transaction,
    Wallet,
} from '../../common/api-interfaces'
import { toStartCase } from '../../common/common.mixin'
import { buildFileUrl } from '../../common/file'
import { User } from '../../common/models/core/user.model'
import { SessionService } from '../../common/services/session.service'
import { InvestmentConfirmationComponent } from './investment-confirmation/investment-confirmation.component'
import { ProductTermsComponent } from './product-terms/product-terms.component'

@Component({
    selector: 'offer',
    templateUrl: 'offer.component.html',
})
export class OfferComponent implements OnInit, OnDestroy {
    public contentBlurred = true
    public toStartCase = toStartCase
    public buildFileUrl = buildFileUrl
    public faFileAlt = faFileAlt
    public faCaretRight = faCaretRight
    public currencies: ICurrency[] = []
    public product: Product
    public files: (File & { productFile: ProductFile })[]

    public allocation: Partial<Allocation> = {
        baseTransaction: {
            type: 'investment',
        } as Transaction,
        product: {} as Product,
    }

    @ViewChild('offerForm')
    public offerForm: NgForm

    private subscriptions = new Subscription()

    constructor(
        private route: ActivatedRoute,
        private http: HttpClient,
        private ngbModal: NgbModal,
        private session: SessionService,
        private router: Router
    ) {}

    public ngOnInit(): void {
        this.route.paramMap.subscribe(params => {
            const modal = this.ngbModal.open(ProductTermsComponent, {
                windowClass: 'modal-primary',
                backdrop: 'static',
                keyboard: false,
            })
            const component = modal.componentInstance as ProductTermsComponent
            const contentRetrievedEvent = new Subject<string | null>()
            component.contentStream = contentRetrievedEvent.asObservable()
            modal.result
                .then(() => {
                    this.contentBlurred = false
                })
                .catch(() => {
                    this.router.navigate(['/discover'])
                })

            this.http
                .get<Product>(`/products/${params.get('productSlug')}`)
                .pipe(
                    tap(product => {
                        contentRetrievedEvent.next(product.terms || null)
                    }),
                    flatMap(product =>
                        forkJoin([
                            of(product),
                            this.http.get<(File & { productFile: ProductFile })[]>(`/products/${product.id}/files`),
                        ])
                    )
                )
                .subscribe(([product, files]) => {
                    this.product = product
                    this.files = sortBy(files, file => file.productFile.purpose)
                    this.allocation.product!.id = product.id
                    this.allocation.baseTransaction!.counterWallet = {
                        product,
                    } as Wallet
                })

            this.subscriptions.add(
                this.session.userStream.subscribe(user => {
                    this.allocation.baseTransaction!.baseWallet = {
                        user: new User(user) as IUser,
                    } as Wallet
                    this.allocation.user = new User(user) as IUser
                })
            )
        })

        this.http
            .get<Paginated<ICurrency>>('/currencies', {
                params: {
                    limit: 100 + '',
                    'filter.type': 'fiat',
                },
            })
            .subscribe(response => {
                this.currencies = response.data
                if (this.currencies.length) {
                    this.allocation.baseTransaction!.currency = this.currencies[0]
                }
            })
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.offerForm.invalid) {
            return
        }
        const modal = this.ngbModal.open(InvestmentConfirmationComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
            keyboard: false,
        })
        const component = modal.componentInstance as InvestmentConfirmationComponent
        component.transaction = this.allocation.baseTransaction!
        component.product = this.product
        component.files = this.files

        component.onConfirm.subscribe(() => {
            this.http
                .post<Allocation>('/allocations', {
                    baseTransaction: {
                        amount: this.allocation.baseTransaction!.amount + '',
                        currency: { code: this.allocation.baseTransaction!.currency.code },
                    },
                    product: { id: this.allocation.product!.id },
                    user: this.allocation.user ? { id: this.allocation.user!.id } : null,
                })
                .subscribe(
                    () => {
                        component.isCompleted = true
                        component.onViewTransactions.subscribe(() => {
                            modal.close()
                            return this.router.navigate(['/discover/investments'])
                        })
                    },
                    () => {
                        component.isSubmitted = false
                        component.reset()
                    }
                )
        })
    }
}
