import { Component, OnInit } from '@angular/core'
import { faFileAlt } from '@fortawesome/pro-regular-svg-icons'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { SgpmxService } from 'src/app/common/services/sgpmx.service'
import { File, Product, ProductFile } from '../../../common/api-interfaces'
import { toStartCase } from '../../../common/common.mixin'
import { buildFileUrl } from '../../../common/file'
import { Currency } from '../../../common/models/accounting/currency.model'
import { PaymentConfirmationComponent } from '../../../common/payment/payment-confirmation/payment-confirmation.component'
import { IntercomService } from '../../../common/services/intercom.service'
import { SessionService } from '../../../common/services/session.service'
import { TokenService } from '../../../common/services/token.service'

@Component({
    selector: 'investment-confirmation',
    templateUrl: 'investment-confirmation.component.html',
})
export class InvestmentConfirmationComponent extends PaymentConfirmationComponent implements OnInit {
    public toStartCase = toStartCase
    public buildFileUrl = buildFileUrl
    public faFileAlt = faFileAlt
    public product: Product
    public files: (File & { productFile: ProductFile })[]
    public documentsRead = false
    public amount: string

    constructor(
        modal: NgbActiveModal,
        tokenService: TokenService,
        session: SessionService,
        sgpmxService: SgpmxService,
        translate: TranslateService,
        public intercom: IntercomService
    ) {
        super(modal, tokenService, session, sgpmxService, translate)
    }

    public ngOnInit(): void {
        super.ngOnInit()
        this.amount = Currency.convert(this.transaction.amount, this.transaction.currency, this.product.fundingCurrency)
    }

    public calculateQuantity(amount: string, price: string): string {
        return new BigNumber(amount).div(price).toString()
    }
}
