import { Component, ElementRef, Input, OnDestroy, OnInit } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Observable } from 'rxjs'
import { tap } from 'rxjs/operators'
import { ToastrService } from '../../../common/services/toastr.service'

const ease = require('ease-component')
const scroll = require('scroll')

@Component({
    selector: 'product-terms',
    templateUrl: 'product-terms.component.html',
})
export class ProductTermsComponent implements OnInit, OnDestroy {
    @Input()
    public contentStream: Observable<string | null>
    public isDown = false
    private isLoaded = false
    private modalContainer: HTMLElement

    constructor(
        private element: ElementRef,
        private toastr: ToastrService,
        public activeModal: NgbActiveModal,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.contentStream = this.contentStream.pipe(
            tap(() => {
                this.isLoaded = true
                setTimeout(() => {
                    this.scrollHandler()
                })
            })
        )
        this.modalContainer = this.element.nativeElement.querySelector('.terms-container') as HTMLElement
        this.modalContainer.addEventListener('scroll', this.scrollHandler)
    }

    public ngOnDestroy(): void {
        this.modalContainer.removeEventListener('scroll', this.scrollHandler)
    }

    public scrollDown(): void {
        scroll.top(this.modalContainer, this.modalContainer.scrollHeight - this.modalContainer.clientHeight, {
            duration: 1000,
            ease: ease.inOutExpo,
        })
    }

    public accept(): void {
        if (!this.isDown) {
            this.toastr.warning(
                this.translate.instant('common.please-scroll-to-bottom'),
                this.translate.instant('common.terms')
            )
            return
        }
        this.activeModal.close()
    }

    private scrollHandler = () => {
        // 25px threshold
        if (
            this.isLoaded &&
            !this.isDown &&
            this.modalContainer.scrollTop >= this.modalContainer.scrollHeight - this.modalContainer.offsetHeight - 25
        ) {
            setTimeout(() => {
                this.isDown = true
            })
        }
    }
}
