import { HttpResponse } from '@angular/common/http'
import { Component } from '@angular/core'
import { faClipboardCheck, faCreditCardFront, faShoppingCart, faTruck } from '@fortawesome/pro-light-svg-icons'
import { faEllipsisV } from '@fortawesome/pro-regular-svg-icons'
import { Observable, of } from 'rxjs'
import { flatMap, tap, withLatestFrom } from 'rxjs/operators'
import { AllocationsComponent } from '../../common/allocations/allocations.component'
import { IUser } from '../../common/api-interfaces'
import { buildFileUrl } from '../../common/file'
import { User } from '../../common/models/core/user.model'

@Component({
    selector: 'user-allocations',
    templateUrl: 'user-allocations.component.html',
})
export class UserAllocationsComponent extends AllocationsComponent {
    public faEllipsisV = faEllipsisV
    public faCreditCardFront = faCreditCardFront
    public faClipboardCheck = faClipboardCheck
    public faTruck = faTruck
    public faShoppingCart = faShoppingCart
    public buildFileUrl = buildFileUrl
    public user: IUser | null

    protected apiQuery = ['status']
    protected defaultQuery = { status: {} }

    public find(): Observable<HttpResponse<any[]>> {
        return of(undefined).pipe(
            withLatestFrom(this.session.userStream),
            tap(([_, user]) => {
                this.user = new User(user)
                this.apiUrl = `/users/${user.id}/allocations`
            }),
            flatMap(() => super.find())
        )
    }
}
